@import "../../common/styles/sass/variables";
@import "../../common/styles/sass/mixins";

.footerBlock {
    background-color: $bgColor;

    .footerContainer {
        display: flex;
        flex-direction: column;

        .boxes {
            display: flex;
            width: 100%;
            justify-content: space-around;
            margin-bottom: 20px;
            flex-wrap: wrap;

            .box {
                @include box();
                padding: 10px;
                width: 100px;
                height: 100px;
            }
        }

        .footerCopyright {
            color: #878e99;
            font-size: 18px;
            font-family: "Poppins", sans-serif;
            width: 100%;
            text-align: center;
            padding: 40px 0;
        }
    }
}